
.zmouse-select{
	width: 94%;
	margin: 5px auto;
}
.zMouse-main{
	width: 100%;
	position: relative;
}
.select-input{
	width: 100%;
	height: 36px;
	line-height: 36px;
	border-bottom: 1px solid rgba(0,0,0,.5);
	position: relative;
}
.select-input .valuestyle{
	color: #666;
	font-size: 18px;
	padding-left: 10px;
}
.select-input .triangle{
	border: 8px solid #fff;
	border-top:8px solid #000;
	position: absolute;
	right: 10px;
	top: 14px;
}
.select-options{
	width: 100%;
	border: 1px solid #ccc;
	border-top: none;
	position: absolute;
	left: 0;
	top: 40px;
	box-shadow: 5px 5px 5px #ccc;
	z-index: 99;
	background: #fff;
}
.select-options li{
	width: 100%;
	height: 40px;
	line-height: 40px;
	text-indent: 10px;
	font-size: 18px;
	color: #666;
}
.selectfage-enter-active{
	transform-origin: 0 0 ;
	animation: goin .3s linear;
}
.selectfage-leave-active{
	transform-origin: 0 0 ;
	animation: goout .3s linear;
}
@keyframes goin {
0%{
		transform: scaleY(0)
}
100%{
		transform: scaleY(1)
}
}
@keyframes goout {
0%{
		transform: scaleY(1)
}
100%{
		transform: scaleY(0)
}
}



