
.zMouse-zezhao{
	width: 100%;
	height: 100%;
	position: absolute;	
	z-index: 2;
}
.backmask{
	background: rgba(0,0,0,.1);
}
.zMouse-aside{
	height: 100%;
	position: fixed;
	background:#fff;
	width: 65%;
	left: 0;
	top: 0;
	z-index: 9999;
}
.fage-enter-active{
	animation: usegoin .5s linear;
}
.fage-leave-active{
	animation: usegoout .5s linear;
}
@keyframes usegoin {
0%{
		transform: translateX(-65%)
}
100%{
		transform: translateX(0px)
}
}
@keyframes usegoout {
0%{
		transform: translateX(0px)
}
100%{
		transform: translateX(-100%)
}
}




